  import React from 'react';
  import { useState } from 'react';
  import { Button, Card, ListGroup, Form, Alert } from 'react-bootstrap';

  import { text } from '../convenience/Text.js';
  import {toQ,toA,toH} from '../convenience/Function.js'


export default function Challenges() {

  const [index,setIndex] = useState(Math.floor( Math.random() * text.length ));
  const [yourAnswer,setYourAnswer]=useState('');
  const [nowYourAnswer,setNowYourAnswer]=useState('');
  const [isStatu,setStatu]=useState(0);

  const submit = (e) => {
    setYourAnswer(nowYourAnswer);
    setStatu(nowYourAnswer==text[index]?1:-1);
    e.preventDafault();
  }

  const setNext = () => {
    setIndex(Math.floor( Math.random() * text.length ));
    setYourAnswer("");
    setNowYourAnswer("");
    setStatu(0);
  }

  const Change = (e) => {
    setNowYourAnswer(e.target.value);
  }

  return (
    <div>
      <br></br>
      <Card style={{ width: '36rem' }}>
        <ListGroup variant="flush">
          <ListGroup.Item>問題：{toQ(text[index])}</ListGroup.Item>
        </ListGroup>
      </Card>

      <br></br>

      <Form onSubmit={submit}>
        <Form.Group controlId="formBasicEmail">
          <Form.Label>Enter Your Answer !</Form.Label>
          <Form.Control placeholder="ここに入力" onChange={Change} value={nowYourAnswer} />
        </Form.Group>

        {isStatu===1 && <Alert variant='success'>Accepted</Alert>}
        {isStatu===-1 && <Alert variant='danger'>Wrong Answer : {yourAnswer}</Alert>}

        <Button variant="primary" type="submit">Submit</Button>{' '}
        <Button variant="primary" onClick={setNext}>Next</Button>
      </Form>

    </div>
  );
}